<template>
    <div>
        <van-tabs v-model="area" line-height='1px' sticky title-active-color='#dd6d60' line-width='0' :border='false'>
        <van-tab title="" name=""></van-tab>
        <van-tab title="" name=""></van-tab>
        <van-tab title="" name=""></van-tab>
        <van-tab title="" name=""></van-tab>
        <van-tab title="" name=""></van-tab>
        <van-tab title="" name=""></van-tab>
        </van-tabs>
         <van-tabs v-model="type" line-height='0px' sticky title-active-color='#dd6d60' line-width='0' :border='false'>
        <van-tab title="" name=""></van-tab>
        <van-tab title="" name=""></van-tab>
        <van-tab title="" name=""></van-tab>
        <van-tab title="" name=""></van-tab>
        <van-tab title="" name=""></van-tab>
        </van-tabs>
          <van-tabs v-model="order" line-height='0px' sticky title-active-color='#dd6d60' line-width='0' :border='false'>
        <van-tab title="" name=""></van-tab>
        <van-tab title="" name=""></van-tab>
        <van-tab title="" name=""></van-tab>
        <van-list  v-model="loading"   :finished="finished" finished-text=""  @load="onLoad"
            loading-text='' offset='100'>
                  <!-- <div class='list-title' v-if="mvtotal!=0"> {{mvtotal}}...</div>
                 <div class='list-title' v-else> </div> -->
                 <div class="gedan-box">
                    <div class="item" v-for="(item,index) in mvList" :key="index" @click="toMv(item.id)">
                        <img :src="item.cover" alt="">
                        <div v-if="item.description">{{item.description}}</div>
                        <div v-else>{{item.name}}</div>
                        <span class="iconfont icon-play3">{{ item.playCount >= 100000 ? parseInt(item.playCount/10000) +'' : item.playCount }} </span>
                    </div>
                </div>
        </van-list>
        </van-tabs>
        
    </div>
    
</template>

<script>
    export default {
        data(){
            return{
                area:'',
                type:'',
                order:'',
                
                limit: 10,
                page: 1,
                total: 0,
                mvList: [],
                 loading: false,
                finished: false
            }
        },
        created() {
            this.getData();
        },
        watch: {
            area() {
                this.mvList =[]
                this.page = 1;
                this.getData()
                
            },
            
            type() {
                this.mvList =[]
                this.page = 1;
                 this.getData()
                 
            },
          
            
            order() {
                this.mvList =[]
                this.page = 1;
                 this.getData()
                 
            },
           
            
        },
        methods: {
            getData() {
               
             this.$http.get('/mv/all', {params:{limit:this.limit ,area:this.area, order:this.order ,offset: (this.page - 1) * this.limit}} ).then(res=>{
                this.mvList.push(...res.data.data) 
                 console.log(res)
                if (res.data.count) {
                this.total = res.data.count;
             }
               
           })
            
            },
               onLoad() {
                 this.page++
                 this.getData()
                this.loading = false;

               
           
            },
            toMv(id) {
                 this.$router.push({path:'/mvdetail',query:{mvid:id}})
            }
        }
 }
</script>
 

<style lang="less" scoped>



</style>